import React, { useEffect, useRef, useState } from "react";
import cog from "./../cog.png";

function Display({
  setting,
  setsetting,
  pomo,
  setPomo,
  short,
  setShort,
  long,
  setlong,
  switchMode,
  color,
  fontStyle
}) {
  const [pause, setPause] = useState(false);
  const [font, setfont] = useState("'Roboto', sans-serif")
  let intervalRef = useRef();


  useEffect(() => {
    return fontStyle === 1
      ? setfont("'Roboto', sans-serif")
      : fontStyle === 2
      ? setfont("'Festive', cursive")
      : setfont("'Abril Fatface', cursive")
  }, [fontStyle]);

  function checkState() {
    if (pomo === 0 && switchMode === 0) return;
    if (short === 0 && switchMode === 1) return;
    if (long === 0 && switchMode === 2) return;
  }

  
  function checkInterval() {
    return (prev) =>
      prev < 1 ? (console.log('finally stop'),clearInterval(intervalRef.current),(setPause(false)), (prev = 0)) : prev - 1;
      //there one bug here cant fix
  }


  const decreaseNum =() => {
    checkState();
    console.log("decream...");
    if (switchMode === 0) return setPomo(checkInterval());
    if (switchMode === 1) return setShort(checkInterval());
    if (switchMode === 2) return setlong(checkInterval());
  };

  useEffect(() => {
    intervalRef.current = setInterval(decreaseNum, 1000);
    return () => clearInterval(intervalRef.current);
  }, []);

  const handleClick = () => {
    if (pause) {
      clearInterval(intervalRef.current);

      console.log("stop...");
    } else {
      checkState();
      intervalRef.current = setInterval(decreaseNum, 1000);
      console.log("running...");
    }
    setPause((prev) => !prev);
  };

  useEffect(() => {
    clearInterval(intervalRef.current);
    setPause(false);
    console.log("stop2. ..");
    return () => clearInterval(intervalRef.current);
  }, [switchMode]);

  useEffect(() => {
    if(setting){
      clearInterval(intervalRef.current);
    setPause(false);
    
      console.log("setting display. ..");
    }
    return () => clearInterval(intervalRef.current);
  }, [setting])


  return (
    <>
      <div className="display">
        <div 
        style={
         pause ? { animation:`bkra${color} 1s infinite alternate-reverse` } : { }
        }
        
      
        className={
        color===2 ? "bk-radius switch-blue":
        color===1 ? "bk-radius switch-red":
        "bk-radius" 
      }>
          <div className="border" onClick={handleClick}>
            <div className="time" style={{fontFamily:font}}  >
              {switchMode === 0 ? pomo : ""}
              {switchMode === 1 ? short : ""}
              {switchMode === 2 ? long : ""}
            </div>
            <span style={{fontFamily:font}} >{pause ? "PAUSE":"START"}</span>
          </div>
        </div>
      </div>
      <div className="setting-icon" onClick={() => setsetting(!setting)}>
        <img src={cog} alt="setting" />
      </div>
    </>
  );
}

export default Display;
