import React from "react";
import close from "./../close.png";
import brush from "./../brush.png";
function Setting({
  setPomo,
  setShort,
  setlong,
  color,
  setcolor,
  setting,
  setsetting,
  fontStyle,
  setfontStyle,
}) {

  

  return (
    <div className="wrapper">
      <div className="setting">
        <div className="head">
          <h2>Settings</h2>
          <div className="x-icon"  onClick={()=>setsetting(!setting)}>
            <img src={close} alt="colse" />
          </div>
        </div>
        <div className="set-time">
          <h4>TIME (MINUTES)</h4>
          <div className="set-time-block">
            <div className="set-wrapper">
              <h5>pomodoro</h5>
              <input type="number" placeholder="60" onChange={(e)=>setPomo(e.target.value)}/>
            </div>
            <div className="set-wrapper">
              <h5>short break</h5>
              <input type="number" placeholder="90" onChange={(e)=>setShort(e.target.value)}/>
            </div>
            <div className="set-wrapper">
              <h5>long break</h5>
              <input type="number" placeholder="90" onChange={(e)=>setlong(e.target.value)}/>
            </div>
          </div>
        </div>
        <div className="font">
          <h3>FONT</h3>
          <div className="round-rapper">
            <div className={fontStyle === 1 ? "select round roboto" : "round roboto"} onClick={()=>setfontStyle(1)}>Aa</div>
            <div className={fontStyle === 2 ? "select round festive" : "round festive"} onClick={()=>setfontStyle(2)}>Aa</div>
            <div className={fontStyle === 3 ? "select round Abril" : "round Abril"} onClick={()=>setfontStyle(3)}>Aa</div>
          </div>
        </div>
        <div className="color">
          <h3>COLOR</h3>
          <div className="round-rapper">
            <div className="round red" onClick={()=>setcolor(1)}>
              {color === 1 ? <img src={brush} alt="V" /> : ""}
            </div>
            <div className="round blue" onClick={()=>setcolor(2)}>
              {color === 2 ? <img src={brush} alt="V" /> : ""}
            </div>
            <div className="round purple" onClick={()=>setcolor(3)}>
              {color === 3 ? <img src={brush} alt="V" /> : ""}
            </div>
          </div>
        </div>
        <div className="btn" onClick={()=>setsetting(!setting)}>Apply</div>
      </div>
    </div>
  );
}

export default Setting;
