import './App.css';
import {useState} from 'react';
import Display from './component/Display';
import Switch from './component/Switch';
import Setting from './component/Setting';


function App() {

  const [pomo,setPomo] = useState(60)
  const [short, setShort] = useState(30)
  const [long, setlong] = useState(90)
  const [switchMode, setswitchMode] = useState(0)
  const [color, setcolor] = useState(3)
  const [setting, setsetting] = useState(false)
  const [fontStyle, setfontStyle] = useState(0)

  const data = {
    pomo,setPomo,
    short,setShort,
    long,setlong,
    switchMode, setswitchMode,
    color,setcolor,
    setting,setsetting,
    fontStyle,setfontStyle
  }


  return (
    <div className="App">
    <Switch {...data}/>
    <Display {...data}/>
    {setting ? <Setting {...data}/> : ''}
    </div>
  );
}

export default App;
