import React, { useState, useEffect } from "react";

function Switch({ switchMode, setswitchMode, color }) {
  const [realColor, setrealColor] = useState("#d881f8");

  
  useEffect(() => {
    return color === 1
      ? setrealColor("#f87070")
      : color === 2
      ? setrealColor("#70f3f8")
      : setrealColor("#d881f8");
  }, [color]);

  return (
    <>
      <h1>pomodoro</h1>
      <div className="switch">
        <div
          style={
            switchMode === 0 ? { background: realColor } : { background:'none' }
          }
  
          onClick={() => setswitchMode(0)}
        >
          pomodoro
        </div>
        <div
          style={
            switchMode === 1 ? { background: realColor } : { background: 'none' }
          }
        
          onClick={() => setswitchMode(1)}
        >
          short break
        </div>
        <div
          style={
            switchMode === 2 ? { background: realColor } : { background: 'none' }
          }
        
          onClick={() => setswitchMode(2)}
        >
          long break
        </div>
      </div>
    </>
  );
}

export default Switch;
